export default {
	data() {
		return {
			trailerList:[]
		}
	},
	onLoad() {
		uni.showLoading({
			title: '搜索中',
			mask: false
		});
		uni.showNavigationBarLoading();
		uni.request({
			url: this.serverURL + '/search/list?qq=843002185&page=&pageSize=&keywords=',
			method: 'POST',
			data: {
			},
			success: res => {
				if (res.data.status == 200) {
					this.trailerList = res.data.data.rows;
				}
			},
			fail: () => {},
			complete: () => {
				uni.hideNavigationBarLoading();
				uni.hideLoading();
			}
		});
	},
	methods: {
		
	}
}
